"use client";

import { useState } from "react";
import { PlusCircle } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Log, mockLogs, mockClients } from "@/data/mock";
import { DataTable } from "./data-table";
import { columns } from "./columns";
import {
  Sheet,
  SheetContent,
  SheetDescription,
  SheetHeader,
  SheetTitle,
  SheetTrigger,
} from "@/components/ui/sheet";
import { useForm } from "react-hook-form";
import * as z from "zod";
import { zodResolver } from "@hookform/resolvers/zod";
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Textarea } from "@/components/ui/textarea";
import { useToast } from "@/hooks/use-toast";

const logFormSchema = z.object({
  clientName: z.string().min(1, "Client selection is required."),
  communicationDate: z.string().refine((val) => !isNaN(Date.parse(val)), {
    message: "Invalid date format",
  }),
  media: z.enum(["Email", "WhatsApp", "Meeting"]),
  notes: z.string().min(10, "Notes must be at least 10 characters long."),
});

type LogFormValues = z.infer<typeof logFormSchema>;

export default function LogsPage() {
  const [logs, setLogs] = useState<Log[]>(mockLogs);
  const [isSheetOpen, setIsSheetOpen] = useState(false);
  const { toast } = useToast();

  const addLog = (newLogData: LogFormValues) => {
    const newLog: Log = {
      id: `LOG${(logs.length + 1).toString().padStart(3, "0")}`,
      ...newLogData,
    };
    setLogs((prev) => [newLog, ...prev]);
    toast({
      title: "Log Added",
      description: `Communication with ${newLog.clientName} has been logged.`,
    });
    setIsSheetOpen(false);
  };

  return (
    <div className="flex flex-col gap-6">
      <div className="flex items-center justify-between">
        <h1 className="font-headline text-3xl font-bold tracking-tight">
          Communication Logs
        </h1>
        <Sheet open={isSheetOpen} onOpenChange={setIsSheetOpen}>
          <SheetTrigger asChild>
            <Button>
              <PlusCircle className="mr-2 h-4 w-4" />
              Add Log
            </Button>
          </SheetTrigger>
          <SheetContent className="sm:max-w-lg">
            <SheetHeader>
              <SheetTitle>Add Communication Log</SheetTitle>
              <SheetDescription>
                Record a new interaction with a client.
              </SheetDescription>
            </SheetHeader>
            <LogForm onSubmit={addLog} />
          </SheetContent>
        </Sheet>
      </div>
      <DataTable columns={columns} data={logs} />
    </div>
  );
}

function LogForm({ onSubmit }: { onSubmit: (data: LogFormValues) => void }) {
  const form = useForm<LogFormValues>({
    resolver: zodResolver(logFormSchema),
    defaultValues: {
      communicationDate: new Date().toISOString().split("T")[0],
      media: "Email",
    },
  });

  return (
    <Form {...form}>
      <form onSubmit={form.handleSubmit(onSubmit)} className="grid gap-4 py-4">
        <FormField control={form.control} name="clientName" render={({ field }) => (
            <FormItem><FormLabel>Client</FormLabel><Select onValueChange={field.onChange} defaultValue={field.value}>
                <FormControl><SelectTrigger><SelectValue placeholder="Select a client" /></SelectTrigger></FormControl>
                <SelectContent>
                    {mockClients.map(client => (
                        <SelectItem key={client.id} value={client.company}>{client.company}</SelectItem>
                    ))}
                </SelectContent>
            </Select><FormMessage /></FormItem>
        )} />
        <FormField control={form.control} name="communicationDate" render={({ field }) => (
            <FormItem><FormLabel>Communication Date</FormLabel><FormControl><Input type="date" {...field} /></FormControl><FormMessage /></FormItem>
        )} />
        <FormField control={form.control} name="media" render={({ field }) => (
            <FormItem><FormLabel>Media</FormLabel><Select onValueChange={field.onChange} defaultValue={field.value}>
                <FormControl><SelectTrigger><SelectValue /></SelectTrigger></FormControl>
                <SelectContent>
                    <SelectItem value="Email">Email</SelectItem>
                    <SelectItem value="WhatsApp">WhatsApp</SelectItem>
                    <SelectItem value="Meeting">Meeting</SelectItem>
                </SelectContent>
            </Select><FormMessage /></FormItem>
        )} />
        <FormField control={form.control} name="notes" render={({ field }) => (
            <FormItem><FormLabel>Notes</FormLabel><FormControl><Textarea {...field} className="min-h-[120px]" /></FormControl><FormMessage /></FormItem>
        )} />

        <Button type="submit" className="w-full mt-4">Save Log</Button>
      </form>
    </Form>
  );
}
